<template>
	<div id="apidessc">
		 <div class="position">
			  <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')"></el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/apidessc','vue-apidessc')">api</el-breadcrumb-item>
			 </el-breadcrumb>
		 </div>
		 <div class="container">
		 	  <h2><span class="l-line"></span>API</h2>
		 	  <div class="container-page">
		 	  	   <p>1) </p>
		 	  	   <p>2) </p>
		 	  	   <p>3)  <font>{{wxhtml}}</font>,

  pos        </p>
                   <p>4) JS-></p>
		 	  </div>
		 	  <h2><span class="l-line"></span></h2>
		 	  <div class="container-page">
		 	  	   <p>1) </p>
		 	  </div>
		 </div>
	</div>
</template>

<script>
    import { bus } from '@/public/bus.js'

	export default{
		data(){
			return{}
		},
		computed:{
			wxhtml(){
				return '<span name="wx_th" flag="wx" class="wxbtn" ></span>';
			}
		},
		methods:{
		    routerPath(path,component){
	             this.$router.push(path);
		         bus.$emit("v-component",component);
		    }
		}
	}
</script>

<style scoped lang="less">
    @size:14px;
    @height:40px;
	.container{
		width:100%;	
		position:relative;
		padding:15px 0;
		h2{
			height: @height;
			line-height:@height;
			font-size:@size;
			text-align:left;
			font-weight: normal;
			padding-left: 15px;
		}
		.l-line{
			display: block;
			position:relative;
		    &:after{
			   display:block;
			   position:absolute;
			   left:-10px;
			   top:0;
               content:"";
               width:3px;
               height:@height;
               background-color: #399cff;
			}
		}
		.container-page{
			padding:0 15px;
			text-align:left;
			font-size:@size;
			p{
				line-height:30px;
			}
			font{
				color:#399cff;
			}
		}
	}

</style>


