<template>
	<div id="project">
		<div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')"></el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/project','vue-project')"></el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="project-tab">
		 	 <div class="box-wrap">
		 	    <el-button class="p-d" type="primary" @click="dialogVisible = true"></el-button>
		        <el-table
				    :data="group.slice((currentPage-1)*pageSize,currentPage*pageSize)"
				    style="width: 100%" v-loading="loading">
				    <template slot="empty">
				            <p>{{dataText}}</p>
				    </template>
				    <el-table-column
				      label="ID"
				      width="40">
				      <template slot-scope="scope">
				        <span style="margin-left: 10px">{{ scope.row.id }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label=""
				      width="280">
				      <template slot-scope="scope">
				        <span>{{ scope.row.project }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label=""
				      width="280">
				      <template slot-scope="scope">
				        <span>{{ scope.row.company }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label=""
				      width="80">
				      <template slot-scope="scope">
				        <span><font class="g-s"></font></span>
				      </template>
				    </el-table-column>
				    <el-table-column label="" width="120">
				      <template slot-scope="scope">
				        <font class="g-d" @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i></font>
				      </template>
				    </el-table-column>
				 </el-table>
		         <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="grouplen">
			    </el-pagination>
			 </div>
		 </div>
		 <div class="project-com">
		 	  <el-dialog
				  title=""
				  :visible.sync="dialogVisible"
				  width="30%">
				  <span class="input-l"></span>
				  <el-input class="input-r" v-model="projectValue" name="groups" placeholder=""></el-input>
				  <span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="projectConfirm" :loading="isloading"> </el-button>
				    <el-button @click="dialogVisible = false"> </el-button>
				  </span>
		     </el-dialog>
		 </div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'
    import {mapGetters,mapActions} from 'vuex'
  
	export default{
		data(){
			return{
                group:[],
                pageSize: 6,
                currentPage: 1,
                loading:true,
                isloading:false,
                projectValue: '',
                dataText: '',
                dialogVisible: false
			}
		},
		mounted(){
			let obj = [];
			let groupData = this.$store.state.group.group;
			let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
			setTimeout(()=>{
				for(var i in groupData){
					obj.push({
						"id": groupData[i].id,
						"project": groupData[i].groups,
						"company": company,
						"status": "",
						"operation": ""
					})
				}
				obj.length === 0 ? this.dataText = '':'';
				this.group = obj;
				this.loading = !this.loading;
			},2000)
		},
		computed:{
            grouplen(){
            	return this.group.length;
            }
	    },
	    watch:{
          '$store.state.group.group'(){
          	  let obj = [];
          	  let groupData = this.$store.state.group.group;
          	  let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
          	  setTimeout(()=>{
          	  	  this.group = [];  //
          	  	  for(var i in groupData){
					obj.push({
						"id": groupData[i].id,
						"project": groupData[i].groups,
						"company": company,
						"status": "",
						"operation": ""
					})
				  }
				  obj.length === 0 ? this.dataText = '':'';
				  this.group = obj;
				  this.loading = false;
          	  },2000)
              //console.log("group",this.$store.state.group.group);
          }
        },
		methods:{
			...mapActions(['getGroup']),
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
			refresh(){ 
	          	  this.loading = true;
	          	  this.getGroup();
	          	  setTimeout(()=>{
	          	  	  let obj = [];
	          	  	  let groupData = this.$store.state.group.group;
	          	  	  let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
	          	  	  for(var i in groupData){
						obj.push({
							"id": groupData[i].id,
							"project": groupData[i].groups,
							"company": company,
							"status": "",
							"operation": ""
						})
					  }
					  obj.length === 0 ? this.dataText = '':'';
					  this.group = obj;
					  this.loading = false;
					  this.$message({
			            type: 'success',
			            message: '!'
			          });
	          	  },2000)
		    },
		    handleDelete(index, row) {
		    	let groupNow = JSON.parse(localStorage.getItem("group")).data;
		    	if(groupNow === row.id){     //
                    this.$message({
		               type: 'warning',
		               message: ''
		            });
		            return;
		    	}
		        this.$confirm('()', '', {
			          confirmButtonText: '',
			          cancelButtonText: '',
			          type: 'warning'
		        }).then(() => {
		             //dele
		              req.post(api.POST_GROUP2,{name:"groups",data:row.id},1).then(res=>{
		              	  //
                          for(let i in res.data){
                          	  post(api.POST_GROUP3,{name:"value",data:resp.data[i]['id']},1).then(res=>{
                          	  	  console.log("!");
                          	  })
			          	  }
			          	  this.getGroup();
		                  this.dialogVisible = false;  
			          	  this.$message({
				            type: 'success',
				            message: '!'
				          });
		              })
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: ''
		          });          
		        });
		    },
		    projectConfirm(){
		    	// this.projectValue
		    	this.isloading = true;
		    	if(this.projectValue){
                    this.$confirm('()', '', {
			          confirmButtonText: '',
			          cancelButtonText: '',
			          type: 'warning'
			        }).then(() => {
			          //
			          for(let i in this.group){
			          	  if(this.group[i].project === this.projectValue){
			          	  	  this.isloading = false;
			          	  	  this.$message({
					            type: 'warning',
					            message: ''
					          });
					          return;
			          	  };
			          }
			          //add
			          req.post(api.POST_GROUP,{name:"groups",data:this.projectValue},1).then(res=>{
                         if(res.status == 200){
			          	  	  this.isloading = false;
					          this.dialogVisible = false;
					          this.getGroup();
	                          this.$message({
					            type: 'success',
					            message: '!'
					          });
			          	  }
			          })
			        }).catch(() => {
			          this.$message({
			            type: 'info',
			            message: ''
			          });          
			        });
		    	}else{
		    	  this.isloading = false;
		    	  this.$message({
		            type: 'warning',
		            message: ''
		          });
		    	}
		    },
		    handleSizeChange(val) {
		        console.log(` ${val} `);
		    },
		    handleCurrentChange(val) {
		        console.log(`1: ${val}`);
		    },
		    deleteOptions(){
                console.log("delete");
		    }
		}
	}
</script>


<style scoped lang="less">
    #bundle(){
    	.pad{
            color:#fff;
            padding:3px;
            font-size:14px;
    	}              
    }
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
	.project-tab{
		margin:15px 0 5px;
		.p-d{
			float:left ! important;
			margin:10px 0;
		}
		font.g-s{
			background-color:#399cff;
			#bundle.pad();
		}
		font.g-d{
			background-color:#FF5722;
			padding:4px ! important;
			cursor: pointer;
            #bundle.pad();
		}
		.el-icon-delete{
			padding-right:3px;
		}
	}
	.input-r{
		width:30%;
		display:inline-block;
		margin-left: 10px;
	}
</style>


